@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;500;700;800&display=swap);
*,::before,::after{margin:0;padding:0;transition:all 0.5s ease-in-out}a{text-decoration:none}body{margin:0;padding:0;box-sizing:border-box;font-family:'Mulish', sans-serif}h1,h2,h3,h4,h5{color:#000000}h4,h5{color:#6e6969;font-weight:normal}.navbar-nav{margin-left:auto}.navbar-brand{font-size:1.5rem;color:#565387 !important}.navbar-brand:hover{color:#3498db !important}.brand_name{color:#3498db}.get-start{display:inline-block;text-transform:capitalize;padding:.7rem 1.5rem;background-color:transparent;border:1px solid #3498db;border-radius:50px;line-height:1}.get-start:hover{background-color:#3498db;color:#ffffff}.active{font-weight:bold;border-bottom:1px solid #3498db}.nav-link:hover{color:#3498db !important}@-webkit-keyframes img-animation{0%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}100%{-webkit-transform:translateY(15px);transform:translateY(15px)}}@keyframes img-animation{0%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}100%{-webkit-transform:translateY(15px);transform:translateY(15px)}}.animated{position:relative;left:13%;width:83%;animation:img-animation 2s infinite alternate-reverse forwards}.card-img{height:180px !important}@media screen and (max-width: 1024px){.animated{left:-5%}.hero-content h1{font-size:1.7rem}.hero-content h5{font-size:1rem}.service-heading{font-size:1.8rem}}@media screen and (max-width: 760px){.service-heading{font-size:1.5rem}.hero-content{text-align:center}.hero-content h1{font-size:1.5rem}.hero-content h5{font-size:1rem}.navbar-toggler{padding:.2rem .5rem}.active{border-bottom:0px !important}.navbar-brand{font-size:1.2rem !important}.animated{left:5%}}

